import express from 'express';
import bp from 'body-parser';
import mongoose from 'mongoose';
import serverless from "serverless-http"
const configURL = 'mongodb+srv://englishlearning362:kiritoEnglish@cluster0.4dz3let.mongodb.net/todoList?retryWrites=true&w=majority';
const PORT = 8888;
const app = express();

app.use(bp.urlencoded({ extended: true }));
app.use(bp.json());

// Create a Mongoose schema
let todoSchema = new mongoose.Schema(
  {
    _id: Number,
    name: String,
    work: String,
    hobby: String,
    age:Number,
    img:Buffer,
  },
  {
    collection: 'todoList', // Replace 'your_collection_name' with the actual collection name
  }
);

// Create a Mongoose model based on the schema
let Todo = mongoose.model('Todo', todoSchema);

// Connect to MongoDB and start the server
(async () => {
  try {
    await mongoose.connect(configURL, { useNewUrlParser: true, useUnifiedTopology: true });
    console.log('Connected to MongoDB');

    app.listen(PORT, () => {
      console.log(`Server is listening on http://localhost:${PORT}`);
    });
  } catch (error) {
    console.error('Failed to connect to MongoDB:', error.message);
  }
})();
app.get('/',(req,res) =>{
  res.send("Welcome")
})
// GET route to fetch all data from the todoList collection
app.get('/todo', async (req, res) => {
  // Fetch all todos from MongoDB using Mongoose
  
  try {
    const todos = await Todo.find();
   
    res.json(todos);
  } catch (error) {
    res.status(500).json({ error: 'Failed to get todos' });
  }
});

app.post('/addNew', async (req, res) => {
  try {
    Todo.collection.insertOne(req.body);
    res.json('success');
  } catch (err) {
    res.status(500).json({ error: err.message });
  }
});

app.delete('/delete', async (req, res) => {
  // Fetch all todos from MongoDB using Mongoose
  try {
    
    const todos = await Todo.findOneAndDelete(req.body);
    res.json("Deleted");
  } catch (error) {
    res.status(500).json({ error: 'Failed to get todos' });
  }
});

export const handler = serverless(app)

